.big-box {
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.images {
    max-width: 200px;
    border-radius: 4px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 35px;
}


